$blue: #000012;
$green: #00ff77;

*, *:before, *:after {
    box-sizing: border-box;
    box-shadow: none;
    color: gainsboro; }

a {
    color: inherit;
    text-decoration: none; }

body {
    background-color: $blue;
    font-family: 'Space Mono', monospace;
    font-size: 1rem;
    line-height: 1.6;
    margin: 0px auto;
    max-width: 80rem;
    overflow-y: scroll;
    #main {
        margin: 0 1rem; } }

fieldset {
    border: none;
    p {
        margin-block-end: 0; } }

footer {
    margin-top: 4rem;
    div {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        div {
            display: flex;
            flex-direction: row;
            font-size: 1rem;
            font-weight: 400;
            margin-bottom: 16px;
            a {
                opacity: 50%;
                &:not(:first-child) {
                    margin-left: 2rem; }
                &:hover {
                    opacity: 100%;
                    i {
                        &:before {
                            color: $green; } } } } } }
    hr {
        border: none;
        padding-bottom: 2rem;
        position: relative;
        &:before {
            color: $green;
            content: '\2022';
            left: 50%;
            position: absolute; } }
    small {
        opacity: 50%; } }

h1 {
    font-size: 1.6rem;
    letter-spacing: 0.2rem; }

h2 {
    font-size: 1.4rem;
    font-weight: 700;
    letter-spacing: 0.2rem;
    text-transform: uppercase; }

h3 {
    font-size: 1.2rem;
    margin-bottom: 0.2rem; }

header {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: 4rem; }

label {
    font-size: 1.2rem;
    font-weight: bold;
    margin: 12px; }

nav {
    align-items: center;
    display: flex;
    flex-direction: row;
    margin-top: 0px;
    button {
        background-color: transparent;
        border: 1px solid gainsboro;
        cursor: pointer;
        font-family: 'Space Mono', monospace;
        font-size: 1.2rem;
        min-height: 2rem;
        min-width: 4rem;
        padding: 0;
        &:not(:first-child) {
            margin-left: 1.2rem; }
        a {
            border: 0.4rem solid transparent;
            display: inline-block;
            height: 100%;
            width: 100%; }
        &:hover {
            background-color: gainsboro;
            color: $blue;
            font-weight: bold; }
        &.active {
            background-color: $green;
            color: $blue;
            font-weight: bold;
            pointer-events: none; } } }

p {
    font-style: normal;
    font-weight: 400;
    hyphens: auto;
    line-height: 1.6;
    margin-block-end: 2rem;
    a {
        border-bottom: 1px dashed hsla(0,0%, 62.7%, 0.50);
        &:hover {
            border-bottom: unset;
            color: $green; } } }

section {
    text-align: center;
    a {
        border-bottom: 1px dashed hsla(0,0%, 62.7%, 0.50);
        &:hover {
            border-bottom: unset;
            color: $green; } }
    div {
        margin-top: 2.6rem;
        text-align: left; }
    #techs {
        display: flex;
        gap: 0.6rem;
        flex-wrap: wrap;
        justify-items: center;
        list-style: none;
        padding: 0;
        vertical-align: center;
        a {
            border: none;
            text-decoration: none;
            &:hover {
                svg {
                    fill-opacity: 1;
                    filter: brightness(0) saturate(100%) invert(91%) sepia(16%) saturate(7402%) hue-rotate(82deg) brightness(101%) contrast(106%); } } }
        li {
            align-items: center;
            display: flex;
            justify-content: center; } } }

span {
    display: block;
    font-size: 0.8rem; }

ul {
    padding-left: 1.2rem;
    li {
        font-size: 0.8rem;
        line-height: 1.4rem; } }

@media screen and (max-width: 600px) {
    header {
        flex-direction: column; } }
